import { useEffect, useState } from 'react';
import classNames from 'classnames';
import './App.css';

function App() {
  const [phones, setPhones] = useState([]);
  const [phone, setPhone] = useState(null)

  useEffect(() => {
    fetch('/api/phones.json')
      .then(response => {
        return response.json()
      })
      .then(response => {
        setPhones(response)
      })
  }, [])

  const handleClick = (id) => {
    fetch(`/api/phones/${id}.json`)
    .then(response => {
      return response.json()
    })
    .then(response => {
      setPhone(response)
    })
  }

  return (
    <div className="App">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-2">
            <section>
              <p>
                Search:
                <input type="text" />
              </p>

              <p>
                Sort by:
                <select>
                  <option value="name">Alphabetical</option>
                  <option value="age">Newest</option>
                </select>
              </p>
            </section>

            <section>
              <p>Shopping Cart</p>
              <ul>
                <li>Phone 1</li>
                <li>Phone 2</li>
                <li>Phone 3</li>
              </ul>
            </section>
          </div>

          <div className="col-md-10">
            <ul className="phones">
              {phones.length > 0 && phones.map(phone => (
                <li key={phone.id} className="thumbnail">
                  <a 
                    href={`#!/phones/${phone.id}`} 
                    className="thumb"
                    onClick={() => handleClick(phone.id)}
                  >
                    <img alt={phone.name} src={phone.imageUrl} />
                  </a>

                  <div className="phones__btn-buy-wrapper">
                    <a className="btn btn-success" href="#buy">
                      Add
                    </a>
                  </div>

                  <a 
                    href={`#!/phones/${phone.id}`}
                    onClick={() => handleClick(phone.id)}
                  >
                    {phone.name}
                  </a>
                  <p>{phone.snippet}</p>
                </li>
              ))}
            </ul>
          </div>
          
          {phone && (
            <div className={classNames("col-md-10", {"col-md-10--active": phone})}>
              <img
                className="phone"
                src={phone.images[0]}
                alt={phone.name}
              />

              <button 
                type="button"
                onClick={() => {
                  setPhone(null)
                }}
              >
                Back</button>
              <button type="button">Add to basket</button>

              <h1>{phone.name}</h1>

              <p>
                {phone.description}
              </p>

              <ul className="phone-thumbs">
                {phone.images.map(image => (
                  <li>
                    <img src={image} alt="" />
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
